@import url("https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Mono:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap");

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');


@import url("https://cdn.jsdelivr.net/npm/@phosphor-icons/web@2.1.1/src/regular/style.css");
@import url("https://cdn.jsdelivr.net/npm/@phosphor-icons/web@2.1.1/src/fill/style.css");
@import url("https://cdn.jsdelivr.net/npm/@phosphor-icons/web@2.1.2/src/duotone/style.css");

:root {
    --black: #101010;
    --code-black: #1e1e1e;
    --light-black: #282828;

    --white: #f0f0f0;
    --light-gray: #cccccc;
    --gray: #b7b7b7;

    --indigo-tint: #e6e9ff;
    --pale-indigo: #c9d0ff;
    --light-indigo: #a5b0ff;
    --accent: #7c8cff;
    --accent-hsl: 232.7 100% 74.3%;
    --darker-indigo: #5f6bd9;
    --deep-indigo: #3d45a6;
    --darkest-indigo: #1e224d;

    --maxw: 1440px;
}

.max_width {
    max-width: var(--maxw);
    margin: auto;
    padding: 60px;

    @media all and (max-width: 800px) {
        padding: 48px 16px;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
li {
    color: var(--white);
    margin: 0;
}

p,
li,
.body {
    color: var(--white);
    font-family: "DM Sans";
    font-size: clamp(18px, 1.25vw, 22px);
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.36px;
}

p.body_small {
    color: var(--white);
    font-family: "DM Sans";
    font-size: clamp(14px, 0.972vw, 18px);
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.36px;
}
i {
    font-size: clamp(18px, 1.25vw, 22px);
    color: var(--white);
}

p.accent,
span.accent {
    color: var(--accent);
}

a {
    color: var(--accent);
    text-decoration: underline transparent;
    text-underline-offset: 3px;
    transition: all ease-in-out 300ms;

    font-family: "Roboto Mono", monospace;
    font-size: clamp(18px, 1.25vw, 22px);
    font-weight: 500;
    line-height: 140%;
    letter-spacing: 0.28px;

    @media all and (max-width: 800px) {
        font-size: clamp(16px, 4.267vw, 18px);
    }

    &:hover {
        color: var(--pale-indigo);
        text-decoration-color: var(--pale-indigo);
    }
}

ul {
    margin: 0;
    /* padding: 0; */
    padding-left: 25px;

    li {
        margin-bottom: 5px;
    }
}

@media all and (max-width: 800px) {
    p,
    li,
    .body {
        font-size: clamp(16px, 4.267vw, 18px);
        letter-spacing: 0.32px;
    }
}

.code,
.code p {
    color: var(--white);
    font-family: "Roboto Mono", monospace;
    font-size: clamp(18px, 1.25vw, 22px);
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.28px;

    @media all and (max-width: 800px) {
        font-size: clamp(16px, 4.267vw, 18px);
    }
}

.code_small,
.code_small p {
    color: var(--white);
    font-family: "Roboto Mono", monospace;
    font-size: clamp(14px, 0.972vw, 18px);
    font-weight: 400;
    line-height: 140%;
    letter-spacing: 0.28px;

    @media all and (max-width: 800px) {
        font-size: clamp(16px, 4.267vw, 18px);
    }
}

/* /////////////////// */
/* Header Styles */
/* /////////////////// */

/* /////////// */
/* Desktop */
/* /////////// */

.xl_header,
h1 {
    font-family: "Roboto Mono", monospace;
    font-size: clamp(60px, 4.861vw, 80px); // 70px
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    letter-spacing: 6.8px;

    @media all and (max-width: 800px) {
        font-size: clamp(36px, 10.667vw, 48px); // 40px
    }
}

.l_header,
h2 {
    font-family: "Roboto Mono", monospace;
    font-size: clamp(45px, 3.819vw, 65px); // 55
    font-style: normal;
    font-weight: 600;
    line-height: 110%;
    letter-spacing: 1.92px;

    @media all and (max-width: 800px) {
        font-size: clamp(40px, 11.2vw, 44px); // 42
        letter-spacing: 1px;
    }
}

.m_header,
h3 {
    font-family: "Roboto Mono", monospace;
    font-size: clamp(34px, 3.056vw, 54px); // 44
    font-style: normal;
    font-weight: 500;
    line-height: 110%;
    letter-spacing: 1px;

    @media all and (max-width: 800px) {
        font-size: clamp(36px, 10.133vw, 40px); // 38
    }
}

.s_header,
h4 {
    font-family: "Roboto Mono", monospace;
    font-size: clamp(25px, 2.431vw, 45px); // 35
    font-style: normal;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0.3px;

    @media all and (max-width: 800px) {
        font-size: clamp(33px, 9.333vw, 37px); // 35
    }
}

.xs_header,
h5,
h6 {
    font-family: "Roboto Mono", monospace;
    font-size: clamp(26px, 1.944vw, 36px); // 28
    font-style: normal;
    font-weight: 400;
    line-height: 110%;
    letter-spacing: 0.72px;

    @media all and (max-width: 800px) {
        font-size: clamp(26px, 7.467vw, 30px); // 28
    }
}

.subtext {
    font-family: "Ostrich Sans";
    font-size: clamp(36px, 2.5vw, 46px);
    font-style: normal;
    font-weight: 900;
    line-height: 120%;
    letter-spacing: 5.2px;
    text-transform: uppercase;

    @media all and (max-width: 800px) {
        font-size: clamp(33px, 9.333vw, 37px); // 35
    }
}

.eyebrow {
    font-family: "Ostrich Sans";
    font-size: clamp(20px, 1.667vw, 30px);
    font-style: normal;
    font-weight: 900;
    line-height: 120%;
    letter-spacing: 3.36px;
    text-transform: uppercase;

    @media all and (max-width: 800px) {
        // font-size: clamp();
    }
}


/* /////////////////// */
/* Overall Styling */
/* /////////////////// */

html,
body {
    height: 100%;
    /* overflow: hidden; */
}

[data-scroll-container] {
    min-height: 100vh;
    position: relative;
}

body {
    background-color: var(--black);
    cursor: url('../data/cursors/cursor-indigo-tint.svg'), auto;
}

/* Links / clickable elements */
a, button, [role="button"] {
  cursor: url('../data/cursors/cursor-click-indigo-tint.svg'), pointer;
}


/* Text selection */
input[type="text"], textarea, [contenteditable="true"] {
  cursor: url('../data/cursors/text.svg'), text;
}

/* Move or drag elements */
.draggable {
  cursor: url('../data/cursors/grab.svg'), grab;
}
.draggable:active {
//   cursor: url('../data/cursors/cursor-grabbing.svg') 36 36, grabbing;
}

/* Resize cursors */
.resizable-ew {
  cursor: url('../data/cursors/resize.svg'), ew-resize;
}
.resizable-ns {
//   cursor: url('../data/cursors/cursor-resize-v.svg') 36 36, ns-resize;
}

